footer {
  position: fixed;
  background: #0e8168;
  width: 100%;
  height: 3rem;
  bottom: 0;

  .navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3rem;

    .menu,
    .cats {
      font-family: agbalumo;
      text-transform: uppercase;
      text-align: center;
      border: solid 0.15rem #ffc861;
      border-radius: 0.5rem;
      background: linear-gradient(to right, #0072ff, #00c6ff);
      padding: 0.3rem 1rem;
      text-decoration: none;
      margin: 1rem;
      width: auto;
      color: #000000;
      box-shadow: 0 0.15rem 0.3rem #1f1e1ef3;
      transition: transform 0.3s ease;

      &:active {
        transform: translateY(-0.5rem);
        background: #fcb83a;
      }
    }
  }

  .service_call {
    font-family: agbalumo;
    text-align: center;
    position: fixed;
    bottom: 0;
    right: 2vw;
    font-size: 0.7rem;
    border: dotted 0.1rem;
    border-color: #535064;
    border-radius: 50%;
    height: 4rem;
    width: 4rem;
    background: #fcb83a;
  }
}
