body {
  margin: 0;
  padding: 0;
  background-attachment: fixed;
  background-image: url("../../public/img/background.jpg");
  background-size: 100% auto;
  background-position-y: 7rem;

  #root {
    overscroll-behavior-y: contain;

    h1,
    p {
      font-family: agbalumo;
    }

    h1 {
      margin-block-start: 0;
      margin-block-end: 0;
    }

    main {
      position: relative;
      z-index: 0;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding-bottom: 5rem;

      .menu {
        padding: 10px 0;
        width: 100%;
        text-align: center;
        background: #b5babbef;
        color: #0e8168;
        border-top: solid 0.2rem;
        border-bottom: solid 0.2rem;
        font-size: 2rem;
        text-shadow: 0.125rem 0.125rem 0.1rem #050505,
          -0.125rem 0.125rem 0.1rem #050505,
           0.125rem -0.125rem 0.1rem #050505,
          -0.125rem -0.125rem 0.1rem #050505;
      }

      .category {
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 10rem;
        margin-top: 2rem;
        width: 100%;
        border-top: solid 0.2rem;
        border-bottom: solid 0.2rem;
        border-color: #0e8168;
        background: #b5babbef;
        overscroll-behavior-x: contain;

        .title {
          display: flex;
          flex-direction: column;
          align-items: center;
          text-align: center;
          text-transform: uppercase;
          width: 20vw;
          font-size: 1rem;
          margin-left: 0.5rem;

          .img {
            filter: drop-shadow(0.1rem 0.1rem 0.2rem #505755);
            height: 6rem;
          }
        }

        .line {
          width: 0.2rem;
          height: 100%;
          background: #0e8168;
        }

        &.coffee .title {
          color: #a85816;
        }

        &.tea .title {
          color: #376b05;
        }

        &.iceTea .title {
          color: #0048d8;
        }

        &.matcha .title {
          color: #0c632d;
        }

        &.smoothies {
          color: #ec3c42;
        }

        &.lemonades .title {
          color: #a59506;
        }

        &.waffles .title {
          color: #af4cac;
        }

        &.toasts .title {
          color: #a38843;
        }

        &.panini .title {
          color: #e69c14;
        }

        &.wraps .title {
          color: #b98005;
        }

        &.desserts .title {
          color: #8b0c58;
        }
      }
    }
  }
}
