header {
  background: #0e8168;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: sticky;
  width: 100%;
  height: 7rem;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;

  .buttons {
    position: absolute;
    font-family: agbalumo;
    letter-spacing: 0.1rem;
    top: 1.3rem;
    left: 3.5vw;
    display: flex;
    flex-direction: column;

    .login,
    .register {
      text-decoration: none;
      text-align: center;
      font-size: 0.6rem;
      color: #000000;
      padding: 0.25rem 1vw;
      border: solid 0.15rem transparent;
      border-radius: 0.2rem;
      filter: drop-shadow(0 0rem 0.1rem #595959);
    }

    .login {
      border-image: linear-gradient(
        to right,
        transparent,
        #fcd678,
        transparent
      );
      border-image-slice: 1;
      background-color: #e1a40b;

      &:active {
        background: #fcd678;
      }
    }

    .register {
      background-color: #c4130d;
      border-image: linear-gradient(
        to right,
        transparent,
        #fa7773,
        transparent
      );
      border-image-slice: 1;
      margin-top: 0.8rem;

      &:active {
        background: #fa7773;
      }
    }
  }

  .company {
    display: flex;
    flex-direction: column;
    align-items: center;

    .logo {
      height: 6rem;
      filter: drop-shadow(0 0.1rem 0.1rem #999191);
    }

    .name {
      margin-top: -1rem;
      height: 2rem;
      filter: drop-shadow(0 0.1rem 0.15rem #fcf9f9);
    }
  }

  .basket {
    background: none;
    border: none;
    right: 5vw;
    top: 1rem;
    position: absolute;

    .img {
      height: 4.5rem;
      width: auto;
      filter: drop-shadow(0 0.1rem 0.1rem #999191);
    }
  }
}
