.auth {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: solid 0.2rem #0e8168;
  width: 80vw;
  margin-top: 0.3rem;
  align-self: center;
  background: #fee497ea;
  border-radius: 1rem;
  font-family: agbalumo;

  .title {
    text-transform: uppercase;
    font-size: 1.2rem;
    color: #0072ff;
    text-decoration: underline;
  }

  .form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;

    .label {
      text-align: center;
      color: #111111;
      font-size: 0.9rem;
    }

    .input {
      padding: 0.5rem;
      border: solid 0.15rem #0e8168;
      border-radius: 0.3rem;
      width: 50vw;
    }

    .button {
      padding: 0.5rem;
      width: 40vw;
      margin-top: 1rem;
      background: linear-gradient(to right, #0072ff, #00c6ff);
      color: white;
      border: none;
      border-radius: 0.3rem;
      cursor: pointer;
      font-size: 0.9rem;
      font-family: agbalumo;
      transition: background-color 0.3s ease;

      &:hover {
        background: #fcb83a;
        color: black;
      }
    }

    .text {
      text-align: center;
      color: #111111;
    }
  }

  .guest {
    font-family: agbalumo;
    padding: 0.5rem 1rem;
    margin-bottom: 0.5rem;
    background: linear-gradient(to right, #0072ff, #00c6ff);
    color: white;
    border: none;
    border-radius: 0.3rem;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;

    &:hover {
      background: #fcb83a;
      color: black;
    }
  }
}
